<template>
<div id="change_password">
  <div class="tit">修改密码</div>
    <div class="df">
      <div class="set_left">输入旧密码</div>
        <div class="set_right">
        <input type="text"  placeholder="请输入旧密码" class="input_km" v-model="password">
        </div>
    </div>
    <div class="df">
      <div class="set_left">输入新密码</div>
        <div class="set_right">
        <input type="password"  placeholder="请输入新密码6~20位" class="input_km" v-model="repass">
        </div>
    </div>
    <div class="df" v-if="user.safety == 1">
      <div class="set_left">输入谷歌验证码</div>
        <div class="set_right">
        <input type="number"  placeholder="请输入谷歌验证码" class="input_km" v-model="safety_code">
        </div>
    </div>
    <div class="set_btns"><div class="set_btn" @click="getform">保存</div></div>
    <Spin fix v-if="spin"></Spin>
</div>
</template>
<script>
    export default {
      data(){
        return {
           spin:false,
           user:JSON.parse(localStorage.getItem('user_info')).data,
           password:'',
           repass:'',
           safety_code:''
        }
      },
      methods: {
        getform(){
          if(this.password.length <6){
            this.$Message.warning("请输入正确的旧密码")
          }else if(this.repass.length < 6){
            this.$Message.warning("请输入6~20位新密码")
          }else if(this.user.safety == 1 && this.safety_code.length < 5){
            this.$Message.warning("请输入正确的谷歌验证码");
          }else{
            this.ajaxform();
          }
        },
        ajaxform(){
          this.spin = true;
          this.$http.post('api/user/change_password',{password:this.password,repass:this.repass,safety_code:this.safety_code}, res => {
                this.spin = false;
               if (res.status == 200) {
                   if(res.data.code == 200){
                     this.$router.push({path:"/main"});
                     this.$Message.success(res.data.msg);
                   }else if(res.data.code == 201){
                     this.$Message.warning(res.data.msg);
                   }
               }
          });
        }
      }
    }
</script>



